// breakpoints
$phone-s: 365;
$phone-m: 414;
$tablet: 767;

// fonts
$font-main: "proxima", sans-serif;
$font-secondary: "rasfire", sans-serif;

// $max-width: 1150px;

:root {
    --black: #000000;
    --white: #ffffff;

    --gray-500: #707070;
    --gray-400: #888888;
    --gray-100: #dfdfdf;

    --red: #FF7C01;
    --secondary: #1B1B1B;

    --gradient: linear-gradient(
        239deg,
        #c913b9 0%,
        #f9373f 50.09%,
        #fecd00 100%
    );
}
