// ANIMATE onSCROLL library -> styles
@import "../../../node_modules/aos/src/sass/aos";

[data-aos="fade-up-15"] {
    transform: translate3d(0, 15%, 0);
    transition-delay: 0.5s;
}

[data-aos="fade-up-25"] {
    transform: translate3d(0, 25%, 0);
    transition-delay: 0.5s;
}

[data-aos="fade-up-50"] {
    transform: translate3d(0, 25%, 0);
    transition-delay: 1s;
}
