.btn { display: flex; justify-content: center; align-items: center; padding: 10px 25px; height: 55px; border-radius: 20px; background-color: var(--white); text-decoration: none; }

@media only screen and (min-width: 414px) { .btn { height: 60px; } }

.btn:hover { opacity: 0.85; }

.btn--red { background-color: var(--red); color: var(--white); }

.btn--secondary { background-color: var(--secondary); color: var(--white); }

.btn--white, .btn--feedback { display: flex; flex-direction: column; font-size: 18px; }

.btn--white:hover strong { color: var(--red); }

.btn--menu { font-size: 20px; letter-spacing: 5px; }

.btn--feedback { background-color: var(--red); }

.btn--feedback svg { fill: currentColor !important; }

.btn--feedback span { opacity: 0.75; }

.btn--feedback span, .btn--feedback strong { color: var(--white) !important; }

.btn span, .btn strong { text-transform: uppercase; }

.btn span { display: flex; align-items: center; color: var(--gray-500); gap: 5px; font-size: 13px; font-weight: 400; letter-spacing: 2px; }

.btn strong { font-weight: 600; font-size: 16px; line-height: 16px; color: var(--black); }

.btn svg { margin-top: -3px; fill: var(--red); width: 17px; height: 17px; }

.home { display: flex; flex-direction: column; height: 100vh; gap: 15px; padding-top: 50px; }

@media only screen and (min-width: 365px) { .home { gap: 25px; } }

@media only screen and (min-width: 414px) { .home { gap: 35px; } }

.home__logo { width: 65%; }

.home__logo img { width: 100%; height: 100%; object-fit: contain; }

.home__center { display: flex; flex-direction: column; align-items: center; gap: 10px; }

.home__center strong { margin-top: -5px; font-size: 16px; font-weight: 400; letter-spacing: 25%; }

.home__bottom { padding-bottom: 20px; }

.home__bottom a:not(:first-child) { margin-top: 10px; }

.home__bottom-table { margin-left: -20px; margin-right: -20px; }

.home__bottom-table img { width: 100%; height: 100%; object-fit: cover; }

.menu { display: flex; flex-direction: column; }

.header { display: flex; align-items: center; height: 75px; }

.header nav { display: flex; align-items: center; justify-content: space-between; flex: 1; padding-left: 25px; padding-right: 25px; }

.header__logo { display: flex; height: 65px; width: auto; }

.header__logo img { height: 100%; }

.header__icon { display: flex; align-items: center; justify-content: center; background-color: var(--white); border-radius: 12px; height: 35px; width: 35px; }

.header__icon svg { width: 20px; height: 20px; }
