.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 25px;

    height: 55px;

    @include rmin($phone-m) {
        height: 60px;
    }

    border-radius: 20px;
    background-color: var(--white);
    text-decoration: none;

    &:hover {
        opacity: 0.85;
    }

    &--red {
        background-color: var(--red);
        color: var(--white);
    }

    &--secondary {
        background-color: var(--secondary);
        color: var(--white);  
    }

    &--white,
    &--feedback {
        display: flex;
        flex-direction: column;
        font-size: 18px;
        // border: 0.5px solid var(--gray-100);
        // box-shadow: 0 15px 50px 0 rgba(0, 0, 0, 0.05);
    }
    &--white:hover strong {
        color: var(--red);
    }

    &--menu {
        font-size: 20px;
        letter-spacing: 5px;
    }

    &--feedback {
        background-color: var(--red);

        svg {
            fill: currentColor !important;
        }

        span {
            opacity: 0.75;
        }
        
        span,
        strong {
            color: var(--white) !important;
        }
    }
}

.btn {
    span,
    strong {
        text-transform: uppercase;
    }

    span {
        display: flex;
        align-items: center;
        color: var(--gray-500);
        gap: 5px;

        font-size: 13px;
        font-weight: 400;
        letter-spacing: 2px;
    }

    strong {
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        color: var(--black);
    }

    svg {
        margin-top: -3px;
        fill: var(--red);
        width: 17px;
        height: 17px;
    }
}
