.home {
    display: flex;
    flex-direction: column;
    // justify-content: space-between;
    height: 100vh;
    gap: 15px;
    padding-top: 50px;

    @include rmin($phone-s) {
        gap: 25px;
    }

    @include rmin($phone-m) {
        gap: 35px;
    }

    &__logo {
        width: 65%;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__top {
    }
    &__center {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;

        strong {
            margin-top: -5px;
            font-size: 16px;
            font-weight: 400;
            letter-spacing: 25%;
        }
    }
    &__bottom {
        padding-bottom: 20px;

        a {
            &:not(:first-child) {
                margin-top: 10px;
            }
        }
    }
    &__bottom-table {
        margin-left: -20px;
        margin-right: -20px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}
