body {
    font-size: 16px;
    font-family: $font-main;
    position: relative;

    // background-image: url("../img/bg.jpg");
    // background-attachment: fixed;
    // background-position: center center;
    // background-size: cover;
    // background-repeat: no-repeat;
    text-decoration: none;

    @include rmin($tablet) {
        margin-left: auto;
        margin-right: auto;
        max-width: 414px;
    }

    &::after {
        content: "";
        position: fixed;
        left: 0;
        top: 0;

        width: 100%;
        height: 100%;
        background-image: url("../img/bg.jpg");
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
        z-index: -1;
    }
}

main {
    height: 100%;
}

.container {
    padding: 0 20px;

    &__title {
        text-align: center;
        text-transform: uppercase;
        // font-family: $font-secondary;
        font-family: $font-main;

        &--1 {
            display: block;
            font-size: 30px;
            line-height: 30px;
        }
        &--2 {
            display: block;
            font-size: 55px;
            line-height: 55px;
            font-weight: 300;
        }
        &--3 {
            display: block;
            font-size: 28px;
            line-height: 23px;
            font-weight: 300;
        }
    }

    &--light {
        padding: 0;
    }
}

@keyframes slide-in-b {
    0% {
        transform: translateY(35px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}
