.header {
    display: flex;
    align-items: center;
    height: 75px;

    nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex: 1;
        padding-left: 25px;
        padding-right: 25px;
    }

    &__logo {
        display: flex;
        height: 65px;
        width: auto;

        img {
            height: 100%;
        }
    }

    &__icon {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: var(--white);
        border-radius: 12px;

        height: 35px;
        width: 35px;

        svg {
            width: 20px;
            height: 20px;
        }
    }
}
